/** &&
 *  @param string id id
 *  @param string key 
 *  @param string value &
 */
export function saveToLocal(id, key, value) {
	// {"123":{"favorite":true},"123456":{"favorite":true}}
	let seller = window.localStorage.__seller__;
	if (!seller) {
		seller = {};
		seller[id] = {};
	} else {
		seller = JSON.parse(seller);
		if (!seller[id]) {
			seller[id] = {};
		}
	}
	seller[id][key] = value;
	// localStroage  
	window.localStorage.__seller__ = JSON.stringify(seller);
};

/** 
 *  @param string id id
 *  @param string key 
 *  @param string def   false
 */
export function loadFromLocal(id, key, def) {
	let seller = window.localStorage.__seller__;
	if (!seller) {
		return def;
	}
	seller = JSON.parse(seller)[id];
	if (!seller) {
		return def;
	}
	let ret = seller[key]; //  true\false 
	return ret || def;
};


